.navbar{
    display:flex;
    width:100%;
    background-color: white;
    height: 100px;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 1px 4px black;
    position: sticky;
    top: 0;
    z-index: 3;
}
.navbar > *{
    padding: 0 30px;
}
.rightSide{
    display:flex;
    flex-direction: row;
    /* justify-content: flex-end; */
}
.rightSide a{
    text-decoration: none;
